import { getData, postData } from './dataUtils';

const conf = {
  url: 'http://158.101.166.74:8080/api/data',
  system: 'illia_cherkasov',
  usersEntity: 'users',
  eventsEntity: 'events',
};

export default class DataLayer {
  constructor() {
    if (DataLayer.exsists) {
      return DataLayer.instance;
    }
    DataLayer.instance = this;
    DataLayer.exsists = true;
    this.url = conf.url;
    this.system = conf.system;
    this.usersEntity = conf.usersEntity;
    this.eventsEntity = conf.eventsEntity;
    this.resStatus = null;
    this.users = [];
    this.events = [];

    this.init();
  }

  init() {
    this.getData(this.usersEntity);
    this.getData(this.eventsEntity);
  }

  async getData(entity) {
    try {
      const res = await getData(entity);
      if (res.status !== 200) { throw new Error(`Entity ${entity} not exists`); }
      const usersData = await res.json();
      getDataFromJSON(usersData, this[entity]);
      console.log(`${entity}: `, this[entity]);
      console.log('emit ok');
    } catch (err) {
      // exeption Decorator
      console.log('emit error');
      console.warn(err);
    }
  }

  async storeData(entity, dataItem) {
    try {
      const res = await postData(entity, JSON.stringify(dataItem));
      if (res.status !== 200) { throw new Error(`Entity ${entity} not exists`); }
      const resData = await res.json();
      // eslint-disable-next-line no-param-reassign
      dataItem.id = resData.id;
      this[entity].push(dataItem);
      console.log(this[entity]);
      console.log('emit render');
    } catch (err) {
      // exeption Decorator
      console.log('emit error');
      console.warn(err);
    }
  }
} // end class DataLayer

function getDataFromJSON(data, list) {
  data.forEach(obj => {
    const item = { id: obj.id, ...JSON.parse(obj.data) };
    list.push(item);
  });
}

